import { createLinePlugin } from './line-indicator-plugin';
export function createSMAPlugin(lineData) {
    return createLinePlugin({
        dataObjectKey: 'sma',
        title: 'SMA',
        lineData: lineData,
        detailMapper: function (key, datum, i) {
            return "SMA " + key + ": " + (datum === 0 ? 0 : datum.toFixed(2));
        },
    });
}
