import { createLinePlugin } from './line-indicator-plugin';
export function createEMAPlugin(lineData) {
    return createLinePlugin({
        dataObjectKey: 'ema',
        title: 'EMA',
        lineData: lineData,
        detailMapper: function (key, datum, i) {
            return "EMA " + key + ": " + (datum === 0 ? 0 : datum.toFixed(2));
        },
    });
}
